import axios from "axios";
import router from "../router";
import qs from "qs";
import cookie from "../utils/cookie";
export let responurl = 'http://39.105.8.210:8080'; 
export let downurl = 'http://39.105.8.210'; 

const instance = axios.create();

let tok = ['studentToken', 'teacherToken', 'adminToken'];

// 请求队列 
const queue = [];
// axios内置的中断ajax的方法
const cancelToken = axios.CancelToken;
// 拼接请求的url和方法，同样的url+方法可以视为相同的请求
const token = (config) => (`${config.url}_${config.method}`);
// 中断重复的请求，并从队列中移除
const removeQueue = (config) => {
  let i = queue.map(q => q.token).indexOf(token(config));
  if (i > -1) {
    queue[i].cancel();
    queue.splice(i, 1);
  }
};

//添加请求拦截器
instance.interceptors.request.use((config, responseType) => {
  removeQueue(config); // 中断之前的同名请求
  // 添加cancelToken
  config.cancelToken = new cancelToken((c) => {
    queue.push({token: token(config), cancel: c});
  });
  let type = localStorage.getItem('type');
  if (type==0) {
    config.headers.studentToken = cookie.getCookie(tok[type]) ;
  }else if(type==1){
    config.headers.teacherToken = cookie.getCookie(tok[type]) ;
  }else if(type==2){
    config.headers.adminToken = cookie.getCookie(tok[type]) ;
  }

  return config;
}, error => {
  return Promise.reject(error);
});

//添加响应拦截器
instance.interceptors.response.use(response => {
    // 在请求完成后，自动移出队列
    let url = response.config.url;
    removeQueue(response.config);
    if (response.data.status.code == 200101) {
      let type = localStorage.getItem('type');
      if (!type) {
        type=0;
      };
      alert('身份认证过期,请重新登陆');
      var urls = ['/login', '/teacherlogin', '/adminlogin'];
      router.replace({path: urls[type]});
    }else{
      return response.data;
    }

  },error => {

    if (!error.response) {
      return Promise.reject({
        msg: '网络连接超时',
        error,
        code: 400
      });
    }

    switch (error.response.status) {
      case 401:
        alert('您没有登录');
        router.replace({path: '/login'});
        break;
      case 403:
        alert('您没有该操作权限');
        break;
      case 500:
        alert('服务器错误');
        break;
    }
    return Promise.reject(error.response.data);
  }
);

/**
 * 封装后的ajax post方法
 *
 * @param {string} url 请求路径
 * @param {object} data 请求参数
 * @param {object} config 用户自定义设置
 * @returns
 */
export const post = (url, data, config = {
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
}) => {
  return instance.post(responurl+url, qs.stringify(data), config);
};

export const post2 = (url, params, config = {
  headers: {
    'Content-Type': 'application/json'
  }
}) => {
  return instance.post(responurl+url, params, config);
};

export const post3 = (url, params, config = {
  headers: {
    'Content-Type':'multipart/form-data'
  }
}) => {
  return instance.post(responurl+'/design/api'+url, params, config);
};
/**
 * 封装后的ajax get方法
 *
 * @param {string} url 请求路径
 * @param {object} params 请求参数
 * @param {object} config 用户自定义设置
 * @returns
 */
export const get = (url, params, config = {
  headers: {
    'Content-Type': 'application/json'
  }
}) => {
  return instance.get(responurl+url, {params}, config);
};



