import Vue from 'vue'
import Router from 'vue-router'
import {fetchCheck_token} from "../axios/requests";
import {cookie} from "../utils";


Vue.use(Router)

const router = new Router({
    // mode:'history',
    routes: [
        {
            path: '/'
        },
        {
            path: '/newcourse/:pid',
            name: 'newcourse',
            component: () => import('../components/page/Snewcourse.vue'),
            meta: { title: '新建教学' }
        },
        {
            path: '/stuworklist/:pid',
            name: 'stuworklist',
            component: () => import('../components/page/Sstuworklist.vue'),
            meta: { title: '课程教学' }
        },
        {
            path: '/workdetail/:id',
            name: 'Sworkdetail',
            component: () => import('../components/page/Sworkdetail.vue'),
            meta: { title: '学生作业' }
        },
        {
            path: '/newworks/:type',
            name: 'newworks',
            component: () => import('../components/page/Snewworks.vue'),
            meta: { title: '新建作业' }
        },
        {
            path: '/stuwork/:id',
            name: 'Tstuwork',
            component: () => import('../components/page/Tstuwork.vue'),
            meta: { title: '课程设计' }
        },
        {
            path: '/piyue/:id',
            name: 'piyue',
            component: () => import('../components/page/Spiyue.vue'),
            meta: { title: '批阅逻辑' }
        },
        {
            path: '/',
            component: () => import('../components/common/Home.vue'),
            meta: { title: '自述文件' },
            children: [
                {
                    path: '/dashboard',
                    component: () => import('../components/page/Dashboard.vue'),
                    meta: { title: '系统首页' }
                },
                {
                    path: '/course',
                    component: () => import('../components/page/Scourse.vue'),
                    meta: { title: '课程设计' }
                },
                
                {
                    path: '/works',
                    component: () => import('../components/page/Sworks.vue'),
                    meta: { title: '作业列表' }
                },
                {
                    path: '/talk',
                    component: () => import('../components/page/Stalk.vue'),
                    meta: { title: '教师讨论区' }
                },
                {
                    path: '/datas',
                    component: () => import('../components/page/Sdatas.vue'),
                    meta: { title: '基础资料' }
                },
                {
                    path: '/message',
                    component: () => import('../components/page/Smessage.vue'),
                    meta: { title: '消息中心' }
                },
                {
                    path: '/stumanage',
                    component: () => import('../components/page/Astumanage.vue'),
                    meta: { title: '学生管理' }
                },
                {
                    path: '/teachermanage',
                    component: () => import('../components/page/Ateachermanage.vue'),
                    meta: { title: '教师管理' }
                },
                {
                    path: '/admin',
                    component: () => import('../components/page/Aadmin.vue'),
                    meta: { title: '管理员管理' }
                },
                {
                    path: '/schoolmanage',
                    component: () => import('../components/page/Aschoolmanage.vue'),
                    meta: { title: '学校组织管理' }
                },
                
                {
                    path: '/stucourse',
                    component: () => import('../components/page/Tstucourse.vue'),
                    meta: { title: '作业详情' }
                },
                {
                    path: '/stumessage',
                    component: () => import('../components/page/Tstumessage.vue'),
                    meta: { title: '消息中心' }
                }
            ]
        },
        {
            path: '/login',
            component: () => import(/* webpackChunkName: "login" */ '../components/page/Login.vue'),
            meta: { title: '学生登录', requireAuth:true }
        },{
            path: '/teacherlogin',
            component: () => import(/* webpackChunkName: "login" */ '../components/page/Login1.vue'),
            meta: { title: '教师登录' , requireAuth:true}
        },{
            path: '/adminlogin',
            component: () => import(/* webpackChunkName: "login" */ '../components/page/Login2.vue'),
            meta: { title: '管理员登录' , requireAuth:true}
        },
        {
            path: '*',
            redirect: '/404'
        }
    ]
})

router.beforeEach(async (to, from, next) => {
  let type = localStorage.getItem('type');
  if (!type) {
    type=0;
  };
  let {title, requireAuth, keywordsType} = to.meta;
  var tokenames = ['studentToken', 'teacherToken', 'adminToken']
  let userToken = cookie.getCookie(tokenames[type]) ? cookie.getCookie(tokenames[type]) : '';
  if (!requireAuth) {
    console.log('路由需要认证');
    if (userToken) {
        if (to.path=="/") {
            var urls = ['/stucourse', '/course', '/stumanage'];
            var tb = urls[type];
            next({ path: tb });
        }else{
            next();
        }
    } else {
      // alert('登录已过期,请重新登录');
      var urls = ['/login', '/teacherlogin', '/adminlogin'];
      router.replace({path: urls[type]});
    }
  } else {
    if (to.path=="/") {
        var urls = ['/stucourse', '/course', '/stumanage'];
        var tb = urls[type];
    };
    next({ path: tb });
  }
});

export default router;


