import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import Loading from 'element-ui'

Vue.use(ElementUI)

Vue.use(router)

Vue.config.productionTip = false;

Vue.prototype.$sortBykey =(ary, key)=>{
  return ary.sort(function (a, b) {
      let x = a[key]
      let y = b[key]
      return ((x < y) ? -1 : (x > y) ? 1 : 0)
    })
}

Vue.prototype.$timeChange = value => {
  if (!value) {
    return '';
  };
  let date = new Date(value);
  let y = date.getFullYear();
  let MM = date.getMonth() + 1;
  MM = MM < 10 ? ('0' + MM) : MM;
  let d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  let h = date.getHours();
  h = h < 10 ? ('0' + h) : h;
  let m = date.getMinutes();
  m = m < 10 ? ('0' + m) : m;
  let s = date.getSeconds();
  s = s < 10 ? ('0' + s) : s;
  return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
};

Vue.prototype.$openBlank = (name, params, type = 0) => {
  let routeData = type === 0 ? router.resolve({
    name,
    params
  }) : router.resolve({
    name,
    query: params
  })
  window.open(routeData.href, '_blank');
};

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



